import React from "react";
import "./styles.css";

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  fetchData(url) {
    fetch(url, {
      headers: { Accept: "application/json" }
    })
      .then((response) => {
        return response.json();
      })
      .then((response) => {
        this.setState({
          response
        });
      });
  }

  componentDidMount() {
    this.fetchData("https://icanhazdadjoke.com/search?page=1");
  }

  render() {
    return (
      <div className="App">
        <h1>Jokes</h1>
        <ul>
          {this.state.response &&
            this.state.response.results.map((joke) => {
              return <li key={joke.id}>{joke.joke}</li>;
            })}
        </ul>
        <p>Page: {this.state.response && this.state.response.current_page}</p>
        {this.state.response && (
          <div>
            <button
              id="previous"
              onClick={() => {
                this.fetchData(
                  `https://icanhazdadjoke.com/search?page=${this.state.response.previous_page}`
                );
              }}
            >
              Previous Page
            </button>
            <button
              id="next"
              onClick={() => {
                this.fetchData(
                  `https://icanhazdadjoke.com/search?page=${this.state.response.next_page}`
                );
              }}
            >
              Next Page
            </button>
          </div>
        )}
      </div>
    );
  }
}
